/**** ABOUT ****/
li#about-link a.nav-current {
  border-color: #f66152;
}
li#about-link a.nav-current h3,
li#about-link a.nav-current i {
  color: #f66152;
}
#about,
#about .cat-pages-title h2 {
  background-color: #f66152;
    opacity: .95;
}
.single-about-page .menu a:hover {
  color: #f66152;
}
.single-about-page .sidebar a:hover {
  color: #f66152;
}
/**** RESUME ****/
li#resume-link a.nav-current {
  border-color: #2980b9;
}
li#resume-link a.nav-current h3,
#resume-link a.nav-current i {
  color: #2980b9;
}
#resume,
#resume .cat-pages-title h2 {
  background-color: #2980b9;
    opacity: .95;
}
.timeline h3 {
  border-bottom-color: #f6db6d;
}
.timeline-left ul {
  border-right-color: #f6db6d;
}
.timeline ul li span:before {
  border-color: #f6db6d;
}
.timeline ul li span:after {
  background-color: #f6db6d;
}
.single-resume-page .menu a:hover {
  color: #d7af0d;
}
.single-resume-page .sidebar a:hover {
  color: #d7af0d;
}
/**** PORTFOLIO ****/
li#portfolio-link a.nav-current {
  border-color: #2ecc71;
}
li#portfolio-link a.nav-current h3,
#portfolio-link a.nav-current i {
  color: #2ecc71;
}
#portfolio,
#portfolio .cat-pages-title h2 {
  background-color: #2ecc71;
    opacity: .95;
    margin-bottom: 0;
}
.quotes ul {
  border-color: #fff;
}
.quotes ul:before {
  background-color: #2ecc71;
}
ol.bjqs-markers li a {
  background-color: #fff;
}
ol.bjqs-markers li.active-marker {
  border-color: #fff;
}
.single-portfolio-page .menu a:hover {
  color: #3498db;
}
.single-portfolio-page .sidebar a:hover {
  color: #3498db;
}
/**** BLOG ****/
li#blog-link a.nav-current {
  border-color: #2cc16b;
}
li#blog-link a.nav-current h3,
#blog-link a.nav-current i {
  color: #2cc16b;
}
#blog,
#blog .cat-pages-title h2,
.blog-single-title h2 {
  background-color: #2cc16b;
    opacity: .95;
}
.ajax-popup {
  background-color: #2cc16b;
}
.single-blog-page .menu a:hover {
  color: #2cc16b;
}
.single-blog-page .sidebar a:hover {
  color: #2cc16b;
}
/**** CONTACT ****/
li#contact-link a.nav-current {
  border-color: #424242;
}
li#contact-link a.nav-current h3,
#contact-link a.nav-current i {
  color: #424242;
}
#contact,
#contact .cat-pages-title h2 {
  background-color: #424242;
    opacity: .95;
}
.single-contact-page .menu a:hover {
  color: #424242;
}
.single-contact-page .sidebar a:hover {
  color: #424242;
}


// Custom Less


  $aboutColor:      #f66152;           // About Background Color
  $resumeColor:     #d7af0d;           // Resume Background Color
  $portfolioColor:  #3498db;           // Portfolio Background Color
  $blogColor:       #2cc16b;           // Blog Background Color
  $contactColor:    #424242;           // Contact Background Color



/**** ABOUT ****/

li#about-link a.nav-current {
  border-color: $aboutColor;
}

li#about-link a.nav-current h3, li#about-link a.nav-current i {
  color: $aboutColor;
}

#about, #about .cat-pages-title h2 {
  background-color: $aboutColor;
}

.single-about-page .menu a:hover { 
  color: $aboutColor; 
}

.single-about-page .sidebar a:hover {
  color: $aboutColor;
}



/**** RESUME ****/

li#resume-link a.nav-current {
  border-color: $resumeColor;
}

li#resume-link a.nav-current h3, #resume-link a.nav-current i {
  color: $resumeColor;
}

#resume, #resume .cat-pages-title h2 {
  background-color: $resumeColor;
}

.timeline h3 {
  border-bottom-color: lighten($resumeColor, 25%);
}

.timeline-left ul {
  border-right-color: lighten($resumeColor, 25%);
}

.timeline ul li span:before {
  border-color: lighten($resumeColor, 25%);
}

.timeline ul li span:after {
  background-color: lighten($resumeColor, 25%);
}

.single-resume-page .menu a:hover { 
  color: $resumeColor; 
}

.single-resume-page .sidebar a:hover {
  color: $resumeColor;
}



/**** PORTFOLIO ****/

li#portfolio-link a.nav-current {
  border-color: $portfolioColor;
}

li#portfolio-link a.nav-current h3, #portfolio-link a.nav-current i {
  color: $portfolioColor;
}

#portfolio, #portfolio .cat-pages-title h2 {
  background-color: $portfolioColor;
}

.quotes ul {
  border-color: lighten($portfolioColor, 25%);
}

.quotes ul:before {
  background-color: $portfolioColor;
}

ol.bjqs-markers li a {
 background-color: lighten($portfolioColor, 25%);
}
ol.bjqs-markers li.active-marker { 
 border-color: lighten($portfolioColor, 25%);
}

.single-portfolio-page .menu a:hover { 
  color: $portfolioColor; 
}

.single-portfolio-page .sidebar a:hover {
  color: $portfolioColor;
}



/**** BLOG ****/

li#blog-link a.nav-current {
  border-color: $blogColor;
}

li#blog-link a.nav-current h3, #blog-link a.nav-current i {
  color: $blogColor;
}

#blog, #blog .cat-pages-title h2, .blog-single-title h2 {
  background-color: $blogColor;
}

.ajax-popup {
  background-color: $blogColor;
}

.single-blog-page .menu a:hover { 
  color: $blogColor; 
}

.single-blog-page .sidebar a:hover {
  color: $blogColor;
}



/**** CONTACT ****/

li#contact-link a.nav-current {
  border-color: $contactColor;
}

li#contact-link a.nav-current h3, #contact-link a.nav-current i {
  color: $contactColor;
}

#contact, #contact .cat-pages-title h2 {
  background-color: $contactColor;
}

.single-contact-page .menu a:hover { 
  color: $contactColor; 
}

.single-contact-page .sidebar a:hover {
  color: $contactColor;
}

